$cell-size: 100px;
$month-length: 31;
$day-shift: 1px;
$week-length: $cell-size * 7 + $day-shift * 6;
$start-days:
  "mon" 0,
  "tue" $cell-size + $day-shift,
  "wed" 2 * ($cell-size + $day-shift),
  "thu" 3 * ($cell-size + $day-shift),
  "fri" 4 * ($cell-size + $day-shift),
  "sat" 5 * ($cell-size + $day-shift),
  "sun" 6 * ($cell-size + $day-shift);
